import React from 'react';
import '../css/About.css'
import show from '../assets/image/info.svg'
import hide from '../assets/image/hide.svg'



class About extends React.Component {
    constructor(){
        super();
        this.state={
            divOpen:false,
        }
    }

    openDiv=()=>{
        if(this.state.divOpen===true)
            this.setState({divOpen:false})
        else
            this.setState({divOpen:true})
    }
    render() {
        const {divOpen} = this.state;
        let styles;
        let info;
        if(divOpen===false){
            styles = {transform:"scale(0,0)",maxHeight:"0px"}
            info=  show

        }
        else{
            styles = {transform:"scale(1,1)",visibility:"visible"}
            info= hide


            

        }
        return (
            <div className="about">
                <div style={styles} className="info-div">
                
                    <fieldset>
                    <legend>Volume Fuction:</legend>
                       <ul>
                       <li>Rotate on Wheel to change volume. Clockwise to increse and Anticlockwise to decrease.</li>
                       <li>This function works when song or video is playing and are on LOCK SCREEN, MUSIC PLAYER, VIDEO PLAYER menu</li>
                       <li>Changes can be seen at notifiaction left icon, It changes to volume icon when something's playing and also shows the current volume.</li>
                       </ul>
                    </fieldset>
                    <fieldset>
                        <legend>Settings:</legend>

                        <fieldset>
                        <legend>Main Setting:</legend>
                        Main setting can be reached form setting option in main menu

                        <ul>
                            <li><b>Theme Setting:</b> Rotating on wheel to change the theme</li>
                            <li><b>Wallpaper Setting:</b> Scroll Down to desired wallpaper and click main button to see changes</li>
                            <li><b>Font Setting:</b> It has five font size, select the anyone to see changes</li>
                        </ul>
                       
                        </fieldset>
                        <fieldset>
                            <legend>Music Setting:</legend>
                            Music setting can be reached from setting option in songs menu
                            <ul>
                                <li><b>Auto Play:</b> Switching on auto play will play next song after current song in ended</li>
                                <li><b>Repeat:</b> If Repeat is ON then next song will be current song</li>
                                <li><b>Suffle:</b> If Repeat is ON then next song will be randon </li>

                            </ul>
                        
                        </fieldset>
                       
                    </fieldset>
                    <fieldset>
                        <legend>Buttons</legend>
                        <ul>
                            <li><b>Menu:</b> Clicking on menu button will take u back to previous menu</li>
                            <li><b>Play/Pause:</b> Clicking on play/pause pauses or play music at any menu except at Video and Game menu</li>
                            <li><b>Next: </b><span>Clicking on next button changes to currently playing media to next media<br/>Pressing, holding and then releasing after few seconds will forward the media playing at any menu. </span></li>
                            <li><b>Previous: </b><span>Clicking on prev button changes to currently playing media to prev media<br/>Pressing, holding and then releasing after few seconds will reverse the media playing at any menu.</span></li>
                            <li><b>Wheel: </b>Rotate wheel to scroll down or up</li>
                            <li><b>Main: </b>Press to select the Menu. It also play pause the media when at Media Screen</li>
                            

                        </ul>
                    </fieldset>
                    <fieldset>
                        <legend>What To observe:</legend>
                        <ul>
                        <li>If music is playing, the Notification bar displays the current song name and a volume icon at top left</li>
                       <li>When at LOCK screen, if music is playing, display will show Sonograph icon</li>
                       <li>Display is divided in two. Information and icon are shown at right when u scroll to different option at left </li>
                        </ul>
                       
                    </fieldset>
                   
                </div>
        <div id="info-btn" onClick={this.openDiv}><img src={info}/></div>
            </div>
        )
    }
}

export default About

